body{
  background-color: #F5F5F5;
}
.App {
  text-align: center;

}

.baner{
  width: 100%;
  height: 65vh;
  margin-top: 15px;
}
.encabezado {
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;
  height: 8vh;
  border-top: 5px solid;
  border-top-color: #00AFF2;
  -webkit-box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.31);
  -moz-box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.31);
  box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.31);
}
.App-logo {
  height: 40vmin;
}

.navegacion a {
  text-decoration: none;
  margin: 5px;
  color: black
}
.base{
  display: flex;
    justify-content: center;
    align-items: center;
}

.panel{
  border-radius: 1.5px;
  width: 95%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 9vh;
    -moz-box-shadow: 0px 0px 0px 0.5px rgba(0,0,0,0.75);
    -webkit-box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.75);
    background-color: #FFFFFF;
}
.menu {
  float: right;
  display: none;
}

.inicial {
  float: right;
  margin-top: 10px;
  margin-right: 25px;
}
.logo {
  float: left;
}

.logo h2 {
  margin: 0;
  margin-top: 10px
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

@media only screen and (max-width: 1000px) {
  /* For tablets: */
  .inicial {
    display: none;
  }
  .menu {
    float: right;
    display: block;
  }
}
.content-section.implementation{
  display: flex;
  justify-content: center;
  align-items: center;
}


.quienSomos{
  width: 50%;
  height: 35vh;
  float: left;
  padding-left: 15px;
}

.fotos{
  width: 50%;
  height: 35vh;
  float: left;
  padding: 10px;
}

.panel2{
  border-radius: 1.5px;

  width: 95%;

    justify-content: center;
    align-items: center;
    -moz-box-shadow: 0px 0px 0px 0.5px rgba(0,0,0,0.75);
    -webkit-box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.75);

}

.react-photo-gallery--gallery img{
  width: 100px;
  height: 100px;
  border-radius: 3em
}

@media only screen and (max-width: 700px){
  .react-photo-gallery--gallery img{
    width: 60px;
    height: 60px;
    border-radius: 3em
  }
}

.modal{
  background-color: rgba(0,0,0,.8);
  position:fixed;
  top:0;
  right:0;
  bottom:0;
  left:0;  
}